.p-home__head {
    display: flex;
    align-items: flex-start;
    h2 {
        margin-right: 2em;
    }
}
.p-home__flow {
    
    .col {
        position: relative;
        &:after {
            content: "";
            background-image: url("data:image/svg+xml,<svg viewBox='0 0 16 16' fill='%23333' xmlns='http://www.w3.org/2000/svg'><path d='M6 12.796V3.204L11.481 8 6 12.796zm.659.753 5.48-4.796a1 1 0 0 0 0-1.506L6.66 2.451C6.011 1.885 5 2.345 5 3.204v9.592a1 1 0 0 0 1.659.753z'/></svg>");
            position: absolute;
            width: 1.1em;
            height: 1.1em;
            bottom: -0.2em;
            right: 0em;
        }
        &:last-child:after {
            display: none;
        }
    }
}
@include media-breakpoint-down(lg) {
    .p-home__head {
        display: block;
    }
}